import Airtable from 'airtable';
import { AIRTABLE_PAT, AIRTABLE_BASE_ID } from '$env/static/private';

// Initialize Airtable with environment variables
export const base = new Airtable({
    apiKey: AIRTABLE_PAT,
    endpointUrl: 'https://api.airtable.com'
}).base(AIRTABLE_BASE_ID);

// Helper function to format Airtable record
export function formatRecord(record) {
    return {
        id: record.id,
        ...record.fields
    };
}

// Helper function to handle Airtable errors
export function handleAirtableError(error) {
    console.error('Airtable error:', error);
    return {
        error: error.message || 'An error occurred while accessing Airtable',
        status: error.statusCode || 500
    };
}
